<template>
    <div>
        <nav-bar title="购物车"/>
        <div class="pay-detail">
            <ul>
                <li class="p-list" v-for="(good,item) in goodObj" :key="item">
                    <mt-switch></mt-switch>
                    <img :src="goods[item].img">
                    <div class="pay-calc">
                        <p>{{goods[item].title}}</p>
                        <div class="calc">
                            <span>￥{{goods.item}}</span>
                            <span @click="substract(item,-1)">-</span>
                            <span>{{good}}</span>
                            <span @click="substract(item,1)">+</span>
                            <a href="javascript:;" @click="del(goodObj)">删除</a>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <div class="show-price">
            <div class="show-1">
                <p>总计(不含运费):</p>
                <span>已经选择商品{payment.count}件，总价￥{payment.total}元</span>
            </div>
            <div class="show-2">
                <mt-button type="danger" size="large" @click="jie">去结算</mt-button>
            </div>
        </div>
    </div>
</template>
<script>
import MyBus from '../../MyBus';
export default {
  async created() {// v-if="item===goodsObj[id]"v-model="good.isSelected"
    this.$axios
      .get(
        "/shu/query?key=33be20415cbf149bd1a0b213d0dcf3e0&catalog_id=242&pn=0&rn=30"
      )
      .then(res => {
        this.goods = res.data.result.data;
        this.goodObj=test.goodsObj;
      })
      .catch(err => console.log(err));

      /*for (var i = 0; i < goods.length; i++) {this.$set(
          if(this.goodsObj.item===i)
          let num=this.goodsObj[item];
          if(num){
              goods.buyNum=num;
              goods.isSelected=true;
          }
          
      }*/
  },
  beforeRouteLeave(to,from,next){
      /*let result=confirm('确定？');
      if(result){*/
          let total=Object.values(test.goodsObj).reduce((
              prev,cur)=>prev+cur);
              //MyBus.$emit('shopcartNum',total);
              this.$store.dispatch('changeNumByAct',total);
              
              next();
      /*}else{
          next(false);
      }*/
  },
  /*computed:{
      payment(){
          let count=0;
          let total=0;
          this.goodObj.reduce(goodObj=>{
              count+=goodObj;
              total+=goodObj*goodObj.item;
          });
          return{
              total,count
          }
      }
  },*/
  data() {
    return {
      goods: [],
      goodObj:[],
    };
  },
  methods:{
      substract(item,yun){
          let num=this.goodObj[item];
          if(yun===-1)
          num--;
          if(yun===1)
          num++;
          this.$set(this.goodObj,item,num);
          this.$store.dispatch('addNumByAct',yun)
          //this.$set(this.goodObj,'isSelected',true);
      },
    //   add(item){
    //       this.goodsObj[item]++;
    //     //   this.$set(this.goodsObj,'buyNum',this.goodsObj[item]);
    //     //   this.$set(this.goodsObj,'isSelected',true);
    //   },
      jie(){
        console.log(this.goods);
        console.log(this.goodObj);
      },
      del(arr,i){
          this.goodObj.splice(i,1);
          delete test.goodsObj[arr.i];
      }
  },
  
}
</script>
<style scoped>
.pay-detail ul li {
    list-style: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    margin-bottom: 3px;
}

.pay-detail ul {
    padding-left: 5px;
    margin-top: 5px;
}

.pay-detail ul li img {
    width: 80px;
    height: 80px;
    display: inline-block;
    vertical-align: top;
    margin-top: 10px;
}

.pay-detail ul li >:nth-child(1),
.pay-detail ul li >:nth-child(3) {
    display: inline-block;
}

.pay-calc p {
    display: inline-block;
    width: 250px;
    overflow: hidden;
    color: blue;
    font-size: 15px;
    margin-bottom: 10px;
}

.pay-detail ul li >:nth-child(1) {
    line-height: 80px;
}

.calc:nth-child(1) {
    color: red;
    font-size: 20px;
}

.calc span:not(:nth-child(1)) {
    border: 1px solid rgba(0, 0, 0, 0.3);
    display: inline-block;
    width: 20px;
    text-align: center;
}

.calc a {
    margin-left: 20px;
}

.show-1,
.show-2 {
    display: inline-block;
}

.show-1,
.show-2 {
    margin-left: 30px;
}

.show-price {
    background-color: rgba(0, 0, 0, 0.2);
}
</style>



