<template>
  <div>
    <my-swipe url="/xin/index?type=top&key=a72d560f3458ae8f9ec1919b933ad2f8"/>

    <div class="grid">
      <my-ul>
        <my-li v-for="(module,index) in modules" :key="index">
          <router-link :to="module.route">
            <span :class="module.className"></span>
            <div>{{module.title}}</div>
          </router-link>
        </my-li>
      </my-ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {

      modules: [
        {
          title: "新闻资讯",
          className: "back-news",
          route: { name: "NewsList" }
        },
        {
          title: "图文分享",
          className: "back-news",
          route: { name: "PhotoList",query:{categoryId:'top'} }
        },
        {
          title: "商品展示",
          className: "back-news",
          route: { name: "GoodsShow",params:{page:6} }
        },
        {
          title: "留言反馈",
          className: "back-news",
          route: { name: "Home" }
        },
        {
          title: "搜索咨询",
          className: "back-news",
          route: { name: "Home" }
        },
        {
          title: "联系我们",
          className: "back-news",
          route: { name: "Home" }
        }
      ]
    };
  },
  
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

/*九宫格*/
.grid span {
  display: inline-block;
  margin: 10px 0;
  width: 50px;
  height: 50px;
  background-repeat: round;
}
.back-img {
  background-image: url(
    https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2219917284,1562241969&fm=26&gp=0.jpg
  );
}
.back-news {
  background-image: url(
    https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2219917284,1562241969&fm=26&gp=0.jpg
  );
}
</style>



