<template>
  <div>
    <nav-bar :title="myTitle" />
    <div class="news-title">
      <p>{{newsInfo.title}}</p>
      <div>
        <span>2次点击</span>
        <span>分类</span>
        <span>添加时间:{{newsInfo.date | convertTime}}</span>
        <a href="/xin/s?id=https%3A%2F%2Fmini.eastday.com%2Fmobile%2F200415141903057.html">asdasd</a>
      </div>
      <div class="news-content" v-html="hhhh"></div>
    </div>
    <div slot="top" class="mint-loadmore-top">
      <span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }">↓</span>
      <span v-show="topStatus === 'loading'">Loading...</span>
    </div>

    <comment :cid="this.$route.params.id"/>

  </div>
</template>

<script>
export default {
  data() {
    return {
      newsInfo: {},
      myTitle: "",
      hhhh: {},
      topStatus: ""
    };
  },
  created() {
    var id = this.$route.params.id;
    //var tttt;
    this.$axios
      .get("/xin/index?type=top&key=a72d560f3458ae8f9ec1919b933ad2f8")
      .then(res => {
        this.newsInfo = res.data.result.data[id];
        //this.tttt=res.data.result.data[id].url;
        // console.log(this.newsInfo.url);
      })
      .catch(err => console.log(err));
  },
  /*methods: {
      handleTopChange(status) {
        this.topStatus = status;
      },
    },*/
  beforeRouteEnter(to, from, next) {
    //新闻详情
    let title = "";
    if (from.name === "NewsList") {
      title = "新闻详情";
    } else if (from.name === "GoodsDetail") {
      title = "商品图文信息";
    } else console.log(from.nmae);
    next(vm => {
      vm.myTitle = title;
    });
  }
};
</script>

<style scoped>
.news-title p {
  color: #0a87f8;
  font-size: 20px;
  font-weight: bold;
}
.news-title span {
  margin-right: 30px;
}
.news-title {
  margin-top: 5px;
  border-bottom: 1px;
}

.news-content {
  padding: 10 5;
}
</style>


