<template>
  <div class="tmpl">
    <div class="photo-bottom">
      <ul>
        <li class="photo-comment">
          <div>
            <span>提交评论</span>
            <span>
              <a>返回</a>
            </span>
          </div>
        </li>
        <li class="txt-comment">
          <textarea style="width:100%" v-model="content"></textarea>
        </li>
        <mt-button type="primary" size="large" @click="sendMSg">发表评论</mt-button>
        <li class="photo-comment">
          <div>
            <span>评论列表</span>
            <span>{{pinglun.length}}条评论</span>
          </div>
        </li>
      </ul>
      <ul class="comment-list">
        <li
          v-for="(pl,index) in pinglun"
          :key="index"
          v-if="index < page"
        >{{pl.author_name}}：{{pl.title}} {{pl.date|convertTime}}</li>
      </ul>
      <mt-button type="danger" size="large" plain @click="loadMsgByPage">加载更多</mt-button>
    </div>
  </div>
</template>
<script>
export default {
  props: ["cid"],
  name: "comment",
  data() {
    return {
      pinglun: [],
      ttt: [{ author_name: "匿名用户", title: this.content }],
      content: "",
      page: 5
    };
  },
  methods: {
    sendMSg() {
      this.pinglun = this.pinglun.concat(this.ttt);
      console.log(this.pinglun);
    },
    loadMsgByPage() {
      this.page = this.page + 5;
      if (this.pinglun.length <= this.page) {
        this.$toast({
          message: "没有评论",
          iconClass: "icon icon-success"
        });
        this.page = this.page - 5;
      }
    }
  },
  created() {
    this.$axios
      .get("/xin/index?type=top&key=a72d560f3458ae8f9ec1919b933ad2f8")
      .then(res => {
        this.pinglun = res.data.result.data; //[this.cid]
        console.log(this.pinglun);
      })
      .catch(err => console.log(err));
  }
};
</script>
<style scoped>
.photo-comment > div span:nth-child(1) {
  float: left;
  font-weight: bold;
  margin-left: 5px;
}

.photo-comment > div span:nth-child(2) {
  float: right;
}

.photo-comment {
  height: 30px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  line-height: 30px;
  margin-bottom: 5px;
}

.txt-comment {
  padding: 5 5;
}

.txt-comment textarea {
  margin-bottom: 5px;
}

.comment-list li {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 5px;
  margin-bottom: 5px;
  padding-left: 5px;
}

li {
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
}
</style>



