<template>
   <div :style="{height:wrapperHeight+'px'}" ref="wrapper">
        <nav-bar title="商品展示"/>
        <mt-loadmore :bottom-method="loadBottom" 
        @bottm-status-change="handleBottomChange" 
        :bottom-all-loaded="allLoaded" :auto-fill="autoFill" 
        ref="loadmore" style="margin-bottom:55px">
        <ul>
            <li v-for="(goods,item) in goodsList" :key="item">
                <router-link :to="{name:'GoodsDetail',params:{id:item}}">
                    <img :src="goods.img">
                    <div class="title">《{{goods.title}}》</div>
                    <div class="desc">
                        <div class="sell">
                            <span>￥80</span>
                            <s>￥9000</s>
                        </div>
                        <div class="detail">
                            <div class="hot">
                                热卖中
                            </div>
                            <div class="count">
                                剩99件
                            </div>
                        </div>
                    </div>
                </router-link>
            </li>
        </ul>
        </mt-loadmore>
    </div>
</template>

<script>
export default {
    data(){
        return{
            goodsList:[],
            allLoaded:false,//禁止拉动函数
            autoFill:false,//自动调用loadBottom直到撑满父容器
            page:this.$route.params.page,
            wrapperHeight:0,
        }
    },
    methods:{
        loadBottom(){
            this.$refs.loadmore.onBottomLoaded();
            this.loadGoodsByPage();
            //console.log(this.page);
        },
        handleBottomChange(status){
            //console.log(status)
        },
        loadGoodsByPage(){
            //console.log(this.page);
            this.$axios.get('/shu/query?key=33be20415cbf149bd1a0b213d0dcf3e0&catalog_id=242&pn=0&rn=${this.page}')
        .then(res=>{
            
            if(this.page<=6){
                this.goodsList=res.data.result.data;
            }/*else{
                this.goodsList=this.goodsList.concat(res.data.result.data);
            }
            //this.goodsList=res.data.result.data;*/
            this.page=this.page+6;
            //console.log(this.page);
            //console.log(res.data.result.data);*/
        })
        .catch(console.log)
        }
    },
    created(){
        this.loadGoodsByPage();
        //console.log(this.page);
    },
    mounted(){
        this.wrapperHeight=document.documentElement.clientHeight-
        this.$refs.wrapper.getBoundingClientRect().top;
    }
}
</script>

<style scoped>

ul {
    padding: 0;
    overflow: hidden;
}
li {
    width: 50%;
    float: left;
    padding: 6px;
    box-sizing: border-box;
}


li > a:not(.mui-btn) {
    margin: 0px;
    padding: 0px;
    border: 1px solid #5c5c5c;
    box-shadow: 0 0 4px #666;
    width: 100%;
    display: block;


}

li > a:not(.mui-btn) img {
     width: 100%;
}

.sell > span {
    float: left;
    color: red;
    text-align: left;
}

.detail >.hot {
    float: left;
    text-align: left;
    font-size: 15px;
}

.detail >.count {
    float: right;
    text-align: right;
    font-size: 15px;
}


/*撑开，去除浮动没有的高度*/

.detail {
    overflow: hidden;
}

.desc {
    color: rgba(92, 92, 92, 0.8);
    background-color: rgba(0, 0, 0, 0.2);
}

img {
    height: 200px;
}
</style>



