<template>
  <div>
    <nav-bar :title="title" />
    <div class="demo">
      <ul>
        <li v-for="(item,index) in newsList" :key="index">
          <router-link :to="{name:'NewsDetail',params:{id:index}}">
            <img :src="item.thumbnail_pic_s" />
            <div>
              <span>{{item.title|controllShow(17)}}</span>
              <div class="news-desc">
                <p>点击数:{{index}}</p>
                <p>发表时间:{{item.date | convertTime('YYYY-MM-DD')}}前</p>
              </div>
            </div>
            
          </router-link>
          <div class="line"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "新闻列表",
      newsList: []
    };
  },

  created() {
    this.$axios
      .get("/xin/index?type=top&key=a72d560f3458ae8f9ec1919b933ad2f8")
      .then(res => {
        this.newsList = res.data.result.data;
        console.log(this.newsList);
      })
      .catch(err => console.log(err));
  },

};
</script>

<style>
.demo a {
  display: block;
  width: 100%;
  height: 44px;
  color: #000;
  padding: 10px 15px;
}
.demo img {
  float: left;
  width: 42px;
  height: 42px;
  margin-right: 20px;
}
.demo a div {
  float: left;
  width: 80%;
  height: 55px;
}
.demo span {
  display: block;
  width: 100%;
  font-size: 17px;
  line-height: 21px;
}
.demo a p {
  float: left;
  color: #0bb0f5;
  font-size: 14px;
  line-height: 21px;
}
.demo p:nth-child(2) {
  float: right;
}
.demo ul {
  padding: 0;
}

.cms-s-header {
  background-color: #f7f7f7;
}
.tmpl h1 {
  display: inline-block;
}
.page-title {
  margin-left: 25%;
}
.line {
  margin-top: 0%;
  margin-left: 16px;
  transform: scaleY(0.5);
  border-bottom: 1px solid #c8c7cc;
}
</style>


