<template>
  <div>
    <mt-header title="信息管理系统"></mt-header>
    
    <transition name="fade" mode="out-in">
      <router-view class="tmpl"/>
    </transition>

    <mt-tabbar v-model="selected">
      <mt-tab-item id="Home"  @click.native="change('Home')">
        <img slot="icon" src='https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586672637984&di=95793392a477d5bc592fecfaf407cd55&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2Ffe2a5be541f3c011972f74d3b50b96578116a92c2f63-tK5HwY_fw658'/>
        首页
      </mt-tab-item>
      <mt-tab-item id="Member">
        <img slot="icon" src='https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586672637982&di=5c7ca596161492f80eb7d86082ba602b&imgtype=0&src=http%3A%2F%2Fku.90sjimg.com%2Felement_origin_min_pic%2F00%2F92%2F02%2F5856f1902d9ddf5.jpg' />
        会员
      </mt-tab-item>
      <mt-tab-item id="Shopcart">
        <img slot="icon" src='https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1470099122,3050577015&fm=26&gp=0.jpg' />
        购物车<mt-badge size="small" type="error">{{getNum}}</mt-badge>
      </mt-tab-item>
      <mt-tab-item id="Search">
        <img slot="icon" src='https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586672807823&di=3c0c42a588ef70c045163e48fe13eec1&imgtype=0&src=http%3A%2F%2Fpic.51yuansu.com%2Fpic2%2Fcover%2F00%2F40%2F63%2F58132bf65cb3e_610.jpg'/>
        查找
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import MyBus from './MyBus'
export default {
  computed:{
    ...mapGetters([
      'getNum'
    ])
  },
  name: "App",
  created(){
    
    MyBus.$on('shopcartNum',(num)=>{
      this.totalNum=num;
    });
  },
  data() {
    return {
      selected: "",
    };
  },
    methods:{
    change(val){
      this.$router.push({
        name:val
      })
    }
  },
  watch:{
    //不能使用箭头函数，因为this会向上级绑定
    selected:function(newV,oldV){
      this.$router.push({
        name:newV
      })
    }
  }
};
</script>

<style scoped>
.mint-tabbar{
  position: fixed;
  bottom:0;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



