<template>
  <div>
    <nav-bar title="图文分享" />
    <!-- 引入返回导航 -->
    <div class="photo-header">
      <ul>
        <li v-for="(category,index) in categories" :key="index">
          <a href="javascript:;" @click="navigateToCateById(category.id)">{{category.name}}</a>
        </li>
      </ul>
    </div>
    <div class="photo-list">
      <ul>
        <li v-for="(img,index) in imgs" :key="index">
          <router-link :to="{name:'NewsDetail',params:{id:index}}">
            <img v-lazy="img.thumbnail_pic_s" />
            <p>
              <span>{{img.title}}</span>
              <br />
              <span>{{img.title}}</span>
            </p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imgs: [],
      categories: [
        { id: "top" ,name:'头条'},
        { id: "shehui" ,name:'社会'},
        { id: "guonei" ,name:'国内'},
        { id: "guoji" ,name:'国际'},
        { id: "yule" ,name:'娱乐'},
        { id: "tiyu" ,name:'体育'},
        { id: "junshi" ,name:'军事'},
        { id: "keji" ,name:'科技'},
        { id: "caijing" ,name:'财经'},
        { id: "shishang" ,name:'时尚'}
      ]
    };
  },

  beforeRouteUpdate (to, from, next) {
    let {categoryId}=to.query;
    this.loadImgsById(categoryId);

    next();

    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
  },

  methods: {
    navigateToCateById(id){
      this.$router.push({
        name:'PhotoList',
        query:{categoryId:id}
      })
    },
    loadImgsById(categoryId) {
      this.$axios
        .get(
          "/xin/index?type=" + categoryId + "&key=a72d560f3458ae8f9ec1919b933ad2f8"
        ) 
        .then(res => {
          this.imgs = res.data.result.data;
        })
        .catch(console.log);
    },
  },

  created() {
    let { categoryId } = this.$route.query.categoryId;
    this.loadImgsById(categoryId);
    this.$axios
      .get(
        "/index?type=" + categoryId + "&key=a72d560f3458ae8f9ec1919b933ad2f8"
      ) 
      .then(res => {
        this.imgs = res.data.result.data;
      })
      .catch(console.log);
  }
};
</script>
<style scoped>
.photo-header li {
  list-style: none;
  display: inline-block;
  margin-left: 10px;
  height: 30px;
}

.photo-header ul {
  /*强制不换行*/
  white-space: nowrap;
  overflow-x: auto;
  padding-left: 0px;
  margin: 5;
}

/*下面的图片*/

.photo-list li {
  list-style: none;
  position: relative;
}

.photo-list li img {
  width: 100%;
  height: 230px;
  vertical-align: top;
}

.photo-list ul {
  padding-left: 0px;
  margin: 0;
}

.photo-list p {
  position: absolute;
  bottom: 0px;
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  margin-bottom: 0px;
}

.photo-list p span:nth-child(1) {
  font-weight: bold;
  font-size: 16px;
}

image[lazy=loading] {
  width: 40px;
  height: 300px;
  margin: auto;
}
</style>



