<template>
  <div>
    <nav-bar title="商品详情"></nav-bar>
    <div class="outer-swiper">
      <div class="swiper">
        <my-swipe url="/shu/query?key=33be20415cbf149bd1a0b213d0dcf3e0&catalog_id=242&pn=0&rn=30" />
        <!--<my-swipe :imgs="goodsDetail"/>-->
      </div>
    </div>
    <div class="product-desc">
      <ul>
        <li>
          <span class="product-desc-span">{{goodsDetail.title}}</span>
        </li>
        <li class="price-li">
          市场价：
          <s>￥9999</s> 销售价：
          <span>￥{{goodId}}</span>
        </li>
        <li class="number-li">
          购买数量：
          <span @click="substract">-</span>
          <span>{{buyNum}}</span>
          <span @click="add">+</span>
        </li>
        <li>
          <mt-button type="primary">立即购买</mt-button>
          <mt-button type="danger" @click="addShopcart">加入购物车</mt-button>
        </li>
      </ul>
    </div>
    <transition name="ball" @after-enter="afterEnter">
      <div class="ball" v-if="inShow"></div>
    </transition>
    <div class="product-props">
      <ul>
        <li>商品参数：{{goodsDetail.reading}}</li>
        <li>商品货号：{{goodId}}</li>
        <li>库存情况：999件</li>
        <li>上架时间：{{goodsDetail.bytime}}</li>
      </ul>
    </div>
    <div class="product-info">
      <ul>
        <li>
          <mt-button type="primary" size="large" plain @click="goGoodsInfo">图文介绍</mt-button>
        </li>
        <li>
          <mt-button type="danger" size="large" plain @click="goGoodsComment">商品评论</mt-button>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import {mapActions} from 'vuex';
import GoodsTools from '../../GoodsTools';
export default {
  created() {
    let id = this.$route.params.id;
    this.$axios
      .get(
        "/shu/query?key=33be20415cbf149bd1a0b213d0dcf3e0&catalog_id=242&pn=0&rn=30"
      )
      .then(res => {
        this.goodsDetail = res.data.result.data[id];
      })
      .catch(err => console.log(err));
    /*let imgReq=this.$axios.get('getthumimages/${this.goodsId}');
      let infoReq=this.$axios.get('goods/getinfo/${this.goodsId}');
      this.$axios.aa([imgReq,infoReq])
      .then(
        this.$axios.spread(
          (imgRes,infoRes)=>{
            this.imgs=imgRes.data.message;
            this.info=infoRes.data.message[0];
          })
      ).catch(console.log)*/

    console.log(this.goodsDetail);
  },
  data() {
    return {
      goodsDetail: [],
      goodId: this.$route.params.id,
      inShow: false,
      buyNum: 1
    };
  },
  methods: {
    ...mapActions([
      'addNumByAct',
      'changeNumByAct'
    ]),
    goGoodsInfo() {
      this.$router.push({
        name: "NewsDetail",
        params: {
          goodId: this.$route.query.id
        }
      });
    },
    goGoodsComment() {
      this.$router.push({
        name: "GoodsComment",
        query: {
          id: this.$route.query.id
        }
      });
    },
    substract() {
      if (this.buyNum === 1) return;
      this.buyNum--;
    },
    add() {
      this.buyNum++;
    },
    addShopcart() {
      this.inShow = true;
    },
    afterEnter(){
      this.inShow = false;
      GoodsTools.addShopcart({
        id:this.goodId,
        num:this.buyNum
      });
      this.addNumByAct(this.buyNum);
    }
  }
};
</script>
<style scoped>
.ball-leave{
  opacity: 0;
}
.ball-enter-active {
  animation: bounce-in 1s;
}

@keyframes bounce-in {
  0% {
    transform: translate3d(0, 0, 0);
  }
  50% {
    transform: translate3d(140px, -50px, 0);
  }
  75% {
    transform: translate3d(160px, 0px, 0);
  }
  100% {
    transform: translate3d(140px, 300px, 0);
  }
}

.swiper {
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin: 8px;
  width: 95%;
  border-radius: 15px;
  overflow: hidden;
}

.outer-swiper,
.product-desc,
.product-props,
.product-info {
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin: 3px;
}

/*请ulpadding*/

.outer-swiper ul,
.product-desc ul,
.product-props ul,
.product-info ul {
  padding: 0;
}

.product-desc ul li,
.product-props ul li,
.product-info ul li {
  list-style: none;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.5);
  margin-top: 8px;
}

.product-desc ul > :nth-child(1) span {
  color: blue;
  font-size: 22px;
  font-weight: bold;
}

.product-desc ul > :nth-child(1) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.product-desc ul,
.product-info ul,
.product-props ul {
  padding-left: 10px;
}

.price-li span {
  color: red;
  font-size: 25px;
}

.price-li s {
  margin-right: 16px;
}

/*加减*/

.number-li span {
  display: inline-block;
  border: 2px solid rgba(0, 0, 0, 0.1);
  width: 25px;
}

/*商品参数*/

.product-props ul > :nth-child(1) {
  text-align: left;
}

.product-props ul:not(:nth-child(1)) {
  margin-left: 40px;
}

.product-info ul {
  margin-bottom: 70px;
  padding: 0 5;
}

.number-li span {
  text-align: center;
}

.number-li > :nth-child(2) {
  width: 40px;
}

.ball {
  border-radius: 50%;
  background-color: red;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 440px;
  left: 120px;
  display: inline-block;
  z-index: 9999;
}
</style>



