// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
import '../static/css/global.css'
import Axios from 'axios'
//import Installer from '@/plugins/installer.js'
import MyUl from './components/common/MyUl'
import MyLi from './components/common/MyLi'
import NavBar from './components/common/NavBar'
import Comment from './components/common/Comment'
import MySwipe from './components/common/Swipe'
import Moment from 'moment';
import { Tabbar, TabItem } from 'mint-ui';
import { Indicator } from 'mint-ui';
import store from './store/store';

Vue.component(Tabbar.name, Tabbar);
Vue.component(TabItem.name, TabItem);

Vue.config.productionTip = false

Vue.prototype.$axios=Axios;
//Axios.defaults.baseURL='/api'

Vue.use(Mint);
// import MyMintUIPlugin from './plugins/MyMintUIPlugin'
// Vue.use(MyMintUIPlugin);

//Vue.use(Installer);

Vue.component(MyUl.name,MyUl);
Vue.component(MyLi.name,MyLi);
Vue.component(NavBar.name,NavBar);
Vue.component(Comment.name,Comment);
Vue.component(MySwipe.name,MySwipe);


//日期
Moment.locale('zh-cn');
Vue.filter('convertTime',function(data,formatStr){
  return Moment(data).startOf('hour').fromNow(formatStr);
});

Vue.filter('convertTimes',function(data,formatStr){
  //Moment.locale('zh-cn');
  return moment(data).format(formatStr);
});


//加载
Axios.interceptors.request.use(function(config){
  Indicator.open({
    text: '加载中...',
    spinnerType: 'fading-circle'
  });
  //console.log(config);
  return config;
});

Axios.interceptors.response.use(function(response){
  Indicator.close();
  //console.log(response);
  return response;
})

//控制字数
Vue.filter('controllShow',function(str,num){
  if(str.length<num){
    return str;
  }
  else{
    return str.substr(0,num-1)+'...';
  }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



