<template>
  <mt-swipe :auto="4000" :show-indicators="false">
    <mt-swipe-item v-for="(item,index) in myimgs||this.imgs" :key="index">
      <a :href="item.url">
        <img :src="item.thumbnail_pic_s||item.img" :alt="item.title" />
      </a>
    </mt-swipe-item>
  </mt-swipe>
</template>

<script>
export default {
  name: "my-swipe",
  props: ["url", "imgs"],
  data() {
    return {
      myimgs: []
    };
  },

  created() {
    console.log('===',this.imgs);
    if (this.url) {
      this.$axios
        .get(this.url)
        .then(res => {
          this.myimgs = res.data.result.data;
          console.log(this.myimgs);
        })
        .catch(err => console.log(err));
    }
    console.log(this.imgs);
  }
};
</script>

<style scoped>
.mint-swipe {
  width: 100%;
  height: 200px;
}
.mint-swipe img {
  height: 100%;
  width: 100%;
}
</style>


